"use client";

import { useState, useMemo, useEffect } from "react";
import { useStrengthAnalysis, StockStrength } from "@/hooks/useStocks";
import StrengthTable from "@/components/StrengthTable";
import { Filter, RefreshCcw, Search, X } from "lucide-react";
import { cn } from "@/lib/utils";
import { useFilter } from "@/context/FilterContext";
import FilterDrawer from "@/components/FilterDrawer";
import QuickFiltersToolbar from "@/components/QuickFiltersToolbar";
import { globalFilter } from "@/utils/globalFilter";

export default function StrengthAnalyzerPage() {
    // Local state for API params (if any specific API filters are needed)
    // For now, we fetch all and filter client-side for consistency with other pages
    const { data: stocks, isLoading, refetch } = useStrengthAnalysis({});

    const [search, setSearch] = useState("");
    const [filters, setFilters] = useState<any>({});
    const { isDrawerOpen, closeDrawer, setFilterCount } = useFilter();

    // Sync filter count to context
    useEffect(() => {
        const count = Object.keys(filters).length;
        setFilterCount(count);
    }, [filters, setFilterCount]);

    const handleReset = () => {
        setFilters({});
        setSearch("");
    };

    // Apply Global Filter + Search
    const filteredStocks = useMemo(() => {
        let result = stocks || [];

        // 1. Search
        if (search) {
            const q = search.toLowerCase();
            result = result.filter(s =>
                s.symbol.toLowerCase().includes(q) ||
                s.name?.toLowerCase().includes(q)
            );
        }

        // 2. Filters
        // Cast as StockStrength[] because globalFilter returns Stock[] but preserves properties
        return globalFilter(result, filters) as StockStrength[];
    }, [stocks, search, filters]);

    return (
        <div className="min-h-screen w-full bg-gray-100 dark:bg-slate-900 text-gray-900 dark:text-gray-100 p-4 md:p-6">
            <FilterDrawer
                isOpen={isDrawerOpen}
                onClose={closeDrawer}
                filters={filters}
                setFilters={setFilters}
                onReset={handleReset}
            />

            <div className="max-w-[1600px] mx-auto space-y-6">

                {/* Header Section */}
                <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
                    <div>
                        <h1 className="text-3xl font-semibold text-gray-900 dark:text-gray-100">
                            Strength Analyzer
                        </h1>
                        <p className="text-gray-500 dark:text-gray-400 mt-1">
                            Deep dive into stock strength trends and momentum indicators ({filteredStocks.length})
                        </p>
                    </div>

                    <div className="flex items-center gap-3 w-full md:w-auto">
                        <div className="relative flex-1 md:w-64">
                            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 w-4 h-4" />
                            <input
                                type="text"
                                placeholder="Search symbol..."
                                className="w-full pl-9 pr-4 py-2 rounded-lg border border-gray-200 dark:border-slate-700 bg-white dark:bg-slate-800 text-sm focus:ring-2 focus:ring-blue-500 outline-none transition-shadow"
                                value={search}
                                onChange={(e) => setSearch(e.target.value)}
                            />
                        </div>

                        {Object.keys(filters).length > 0 && (
                            <button
                                onClick={handleReset}
                                className="px-3 py-2 rounded-lg text-sm font-medium text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors flex items-center gap-2 whitespace-nowrap"
                            >
                                <X className="w-4 h-4" /> Clear
                            </button>
                        )}

                        <button
                            onClick={() => refetch()}
                            className="p-2 bg-white dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded-lg hover:bg-gray-50 dark:hover:bg-slate-700 transition-colors text-gray-600 dark:text-gray-300"
                            title="Refresh Data"
                        >
                            <RefreshCcw className="w-5 h-5" />
                        </button>
                    </div>
                </div>

                {/* Main Content Card */}
                <div className="bg-card text-card-foreground rounded-3xl shadow-sm dark:shadow-md border border-border overflow-hidden">
                    <QuickFiltersToolbar
                        filters={filters}
                        setFilters={setFilters}
                        onReset={handleReset}
                    />
                    <div className="p-6 pt-0">
                        <StrengthTable stocks={filteredStocks} isLoading={isLoading} />
                    </div>
                </div>
            </div>
        </div>
    );
}
